// in src/comments.js
import React from "react";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import AccountBox from "@material-ui/icons/AccountBox";
import Avatar from "@material-ui/core/Avatar";
import {
  TextField,
  EditButton,
  List,
  //Create,
  SimpleForm,
  TextInput,
  DisabledInput,
  // LongTextInput
  Edit
} from "react-admin";

const cardStyle = {
  width: 300,
  minHeight: 300,
  margin: "0.5em",
  display: "inline-block",
  verticalAlign: "top"
};

const CommentGrid = ({ ids, data, basePath }) => (
  <div style={{ margin: "1em" }}>
    {ids.map(accountId => (
      <Card key={accountId} style={cardStyle}>
        <CardHeader
          title="MyAccounts"
          //subheader={<DateField record={data[accountId]} source="namabank" />}
          avatar={<Avatar icon={<AccountBox />} />}
        />
        <CardContent>
          Nama :<TextField record={data[accountId]} source="nama" />
          Nama Bank :<TextField record={data[accountId]} source="namabank" />
          Alamat :<TextField record={data[accountId]} source="alamat" />
          HandPhone :<TextField record={data[accountId]} source="hp" />
          Total Pembelian :
          <TextField record={data[accountId]} source="totalpembelian" />
          {/* <ReferenceField
            label="Post"
            resource="comments"
            record={data[id]}
            source="post_id"
            reference="posts"
            basePath={basePath}
          >
            <TextField source="title" />
          </ReferenceField> */}
        </CardContent>
        <CardActions style={{ textAlign: "right" }}>
          <EditButton
            resource="myaccounts"
            basePath={basePath}
            record={data[accountId]}
          />
        </CardActions>
      </Card>
    ))}
  </div>
);
CommentGrid.defaultProps = {
  data: {},
  ids: []
};

export const AccountEdit = props => (
  <Edit {...props}>
    <SimpleForm>
      <TextInput source="nama" />
      <TextInput source="namabank" />
      <TextInput source="alamat" />
      <TextInput source="hp" />
    </SimpleForm>
  </Edit>
);

export const MyaccountList = props => (
  <List title="My Account" {...props}>
    <CommentGrid />
  </List>
);
